import axios from 'axios';



const getUsername = async () => {
    try {
        const response = await axios.get('/getUser', {
            withCredentials: true,
        });
        const isAuthenticated = JSON.stringify(response.data);

        return response.data
    } catch (error) {
        console.error("Login failed:", error);
        return false;
    }
};

const username = await getUsername().then((result) => {
    return result.username;
});




// Call logout 
const handleLogout = () => {
    axios.get('/logout', { withCredentials: true })
        .then(res => {
            window.location.href = '/';
        })
        .catch(error => {
            console.error('Logout error:', error);
        });
};

export default function NavBar() {


    //dispaly the user name



    return (
        <nav className="p-4 m-4">
            <div className="container mx-auto flex justify-between items-center">
                <div className="text-2xl font-bold"><a href="/" className="hover:text-gray-200">AIBGEN</a></div>
                <ul className="space-x-4">
                    <li className="inline-block">
                        <a href="/about" className="hover:text-gray-200">About</a>
                    </li>
                    {username ? (<li className="inline-block">
                        <a href="/dashboard" className="hover:text-gray-200">Dashboard</a>
                    </li>

                    ) : null}
                    {!username ? (
                        <li className="inline-block">
                            <a href="/register" className="hover:text-gray-200">Register</a>
                        </li>) : null}

                    {!username ? (
                        <li className="inline-block">
                            <a href="/login" className="hover:text-gray-200">Login</a>
                        </li>) : null}

                    {username ? (<li className="inline-block">
                        <button onClick={handleLogout} className="hover:text-gray-200">Logout </button>

                    </li>

                    ) : null}



                    <li className="inline-block">
                        {username ? (
                            <span className="text-green-900 hover:text-gray-200">
                                Welcome {username}
                            </span>
                        ) : null}
                    </li>
                </ul>
            </div>
        </nav>

    );
}
